import React, { useState, useEffect } from "react";
import { ethers } from "ethers";
import { motion } from "framer-motion";

const CONTRACT_ADDRESS = "0x01D675A61d94aEa570007374d85301d6619D9E6e";
// DOGE_ADDRESS removed  native DOGE is used via provider.getBalance()
const MCRIB_ADDRESS = "0xbdaD927604c5cB78F15b3669a92Fa5A1427d33a2";

const ABI = [
  // PAYABLE mint (so we can send DOGE as msg.value)
  "function mint(uint256 num) external payable",
  // Dynamic pricing / reveal config
  "function dogeFee() external view returns (uint256)",
  "function mcribPrice() external view returns (uint256)",
  "function revealBlock() external view returns (uint256)",
  "function revealed() external view returns (bool)",
  // Core ERC721-ish stuff
  "function tokenURI(uint256 id) external view returns (string)",
  "function totalSupply() external view returns (uint256)",
  "function ownerOf(uint256 tokenId) external view returns (address)",
  "function balanceOf(address owner) external view returns (uint256)",
  // 
  // NEW ERC721Enumerable FUNCTIONS  FOR RANDOM ID TRACKING
  // 
  "function tokenOfOwnerByIndex(address owner, uint256 index) external view returns (uint256)",
  "function tokenByIndex(uint256 index) external view returns (uint256)",
  "function isEaten(uint256) external view returns (bool)",
  "function tokenBalance(uint256 id) external view returns (uint256)",
  "function eatMcRib(uint256 id) external"
];

const ERC20_ABI = [
  "function approve(address spender, uint256 amount) external returns (bool)",
  "function allowance(address owner, address spender) external view returns (uint256)",
  "function balanceOf(address account) external view returns (uint256)"
];

const TX_OPTIONS = {
  gasPrice: ethers.utils.parseUnits("250", "gwei"),
  // gasLimit removed  now calculated dynamically inside handleMint
};
